  <div id="nav">
    <div id="top-row">
      <a id="music" href="//soundcloud.com/waxedlaces">Music</a>
      <div id="triangle">
        <svg viewBox="0 0 300 260">
          <polygon points="4 256 146 4 296 256 4 256" />
          <text transform="translate(93 161) rotate(-60)">ON</text>
          <text transform="translate(176 115) rotate(60)">THE</text>
          <text transform="translate(125 243)">SLY</text>
        </svg>
      </div>
      <a id="cloth">Cloth</a>
      <img id="ricky-the-wolfe" src="../assets/Easy Impress white-02-02-02.png"/>
      <a id="eyepatch" @click="showWelcome()"></a>
      <div id="welcome" v-bind:class="[{ visible: welcomeIsVisible }]">
        Welcome to the Project
        <a id="close-button" @click="hideWelcome()">×</a>
      </div>
    </div>
    <div id="bottom-row">
      <a id="utility">
        <div>Utility</div>
        <div>×</div>
        <div>Technology</div>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavTriangle',
  props: {
    welcomeIsVisible: Boolean
  },
  methods: {
    showWelcome() {
      this.hideRickyTheWolfe()
      setTimeout(() => this.$emit('toggleWelcome'), 1000)
    },
    hideWelcome() {
      this.$emit('toggleWelcome')
      setTimeout(this.showRickyTheWolfe, 1000)
    },
    showRickyTheWolfe() {
      document.getElementById('ricky-the-wolfe').style.padding = 0
      document.getElementById('eyepatch').style.display = 'block'
    },
    hideRickyTheWolfe() {
      document.getElementById('ricky-the-wolfe').style.padding = '5vw'
      document.getElementById('eyepatch').style.display = 'none'
    }
  }
}
</script>

